import { useState } from "react";

const AddTodo = () => {
  const [text, setText] = useState("");
  const [todo, setTodo] = useState([]);
  const handleChange = (e) => {
    setText(e.target.value);
  };
  const onClick = () => {
    const item = {
      id: Math.random() + text,
      title: text,
      status: true
    };
    setTodo([...todo, item]);
    setText("");
    console.log(todo);
  };
  const handleToggle = (id) => {
    const newTodo = todo.map((ele) =>
      ele.id === id ? { ...ele, status: !ele.status } : ele
    );
    setTodo(newTodo);
  };
  const handleDelete = (id) => {
    const newTodo = todo.filter((ele) => (ele.id !== id ? ele : null));
    setTodo(newTodo);
  };
  return (
    <div>
      <input
        className="input"
        value={text}
        onChange={handleChange}
        type="text"
      />
      <button className="add" onClick={onClick}>
        +
      </button>

      <ul>
        {todo.map((item) => (
          <li key={item.id}>
            {item.title}--
            {item.status ? "Not Completed" : "Completed"}--
            <button onClick={() => handleToggle(item.id)}>Toggle</button>
            <button onClick={() => handleDelete(item.id)}>Delete</button>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default AddTodo;
